@import './_colors';
@import './_fonts';

body {
  color: $white-color;
  font-size: 14px;
  font-family: 'Poppins Regular', Arial, Helvetica, sans-serif;
  background: $black-color; }

h1 {
  margin-bottom: 24px;
  font-size: 48px;
  font-weight: 600; }

h2 {
  position: relative;
  margin-bottom: 24px;
  font-size: 24px;
  font-weight: 600;
  cursor: pointer;

  &::after {
    content: '';
    display: block;
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 30px;
    height: 2px;
    background: $pink-color;
    transition: all 500ms ease; }

  &::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 80px;
    height: 2px;
    background: $black-color; }

  &:hover {
    &::after {
      width: 80px; } } }

h3 {
  margin-top: 40px;
  margin-bottom: 12px;
  color: $cyan-color;
  font-size: 16px; }

h4 {
  font-size: 18px;
  font-weight: 600;

  & ~ span {
    display: block;
    margin-bottom: 12px;
    color: $blue-color;
    line-height: 1.2; } }

p {
  margin-bottom: 4px; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;

  li {
    position: relative;
    margin-bottom: 8px;
    margin-left: 24px;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      display: block;
      left: -20px;
      top: 10px;
      width: 12px;
      height: 2px;
      background: $black-color;
      transition: all 300ms ease; }

    &:hover {
      &::before {
        background: $cyan-color; } } } }

a {
  color: $yellow-color;
  transition: all 300ms ease;

  &:hover {
    color: $cyan-color; } }

.container {
  margin: 40px auto;
  padding: 40px;
  border-radius: 15px;
  background-color: $grey-color;
  box-shadow: 0 0 40px -10px rgba(0, 0, 0, 0.2); }

header {
  margin-bottom: 100px; }

main {
  .row {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0; } } }

footer {
  border-top: 2px solid #333;

  .container {
    margin: 0 auto;
    padding: 12px 0;
    padding-right: var(--bs-gutter-x, 0.75rem);
    padding-left: var(--bs-gutter-x, 0.75rem);
    border-radius: 0;
    background: transparent;

    a {
      margin-right: 12px;
      color: $purple-color;
      font-size: 16px;
      font-weight: 500;
      text-decoration: none;

      &:hover {
        color: $cyan-color; } } } }

.avatar-container {
  overflow: hidden;
  min-width: auto;
  max-width: 100%;
  min-height: auto;
  border: 18px solid $black-color;
  border-radius: 50%;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.5);

  .avatar {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.1); } }

.timeline {
  &__item {
    position: relative;
    padding: 20px;
    border-top: 3px solid;
    border-image: linear-gradient(to right, $purple-color 0%, $cyan-color 100%);
    border-image-slice: 1;
    counter-increment: section;

    &:before {
      content: counter(section);
      position: absolute;
      top: calc(50% - 14px);
      left: -14px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 28px;
      width: 28px;
      font-size: 20px;
      font-weight: 600;
      line-height: 0;
      background-color: $black-color;
      border-radius: 50%; }

    &:nth-child(odd) {
      border-right: 3px solid;

      &:before {
        right: -16px;
        left: auto; } }

    &:nth-child(even) {
      border-left: 3px solid;

      &:before {
        left: -16px;
        right: auto; } }

    &:first-child {
      border-top: 0;
      border-top-right-radius: 0;
      border-top-left-radius: 0; }

    &:last-child {
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0; } } }

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);

  .loader {
    position: absolute;
    top: calc(50% - 24px);
    left: calc(50% - 24px);
    width: 48px;
    height: 48px;
    border: 5px solid $white-color;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite; } }

@keyframes rotation {
  0% {
    transform: rotate(0deg); }

  100% {
    transform: rotate(360deg); } }

@media (min-width: 768px) {
  header {
    padding: 0 100px; } }

@media (min-width: 1200px) {
  h3 {
    margin-top: 100px; } }
